export const formatDate = (dateString: string | undefined): Date | undefined => {
  if (dateString) {
    const [year, month, date] = dateString.split('-');
    return new Date(parseInt(year, 10), parseInt(month, 10) - 1, parseInt(date, 10));
  }

  return undefined;
};

export const formatTime = (date: Date | undefined, timeString: string | undefined): Date | undefined => {
  if (date && timeString) {
    const [hour, minute, second] = timeString.split(':');
    return new Date(
      date.getFullYear(),
      date.getMonth(),
      date.getDate(),
      parseInt(hour, 10),
      parseInt(minute, 10),
      parseInt(second, 10),
    );
  }

  return undefined;
};
